import React from 'react';
import PropTypes from 'prop-types';
import { Panel, PanelHeader, HeaderButton, Button, Link, Div,  platform, IOS, Group, Cell, List} from '@vkontakte/vkui';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';
import Icon24BrowserForward from '@vkontakte/icons/dist/24/browser_forward';
import Icon24Newsfeed from '@vkontakte/icons/dist/24/newsfeed';
import Icon24User from '@vkontakte/icons/dist/24/user';

const osname = platform();

const Pirog = props => (
	<Panel id={props.id}>
		<PanelHeader
			left={<HeaderButton onClick={() => window.history.back()}>
				{osname === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}
			</HeaderButton>}
		>
Лаханоризо		
</PanelHeader>




<Group>
<List>
<Cell before={<Icon24User fill="#4bb34b" />} indicator={<Link component = "a" href = "https://www.spletenie.ru/users/barska/povarenok/">barska</Link>}>Автор  </Cell>
<Cell before={<Icon24Newsfeed fill="#4bb34b" />} indicator={<Link component = "a" href = "https://www.povarenok.ru">povarenok.ru</Link>}>Ресурс </Cell>

</List>
</Group>

<Group>
              <List>
                <Cell indicator="45 минут">Время приготовления </Cell>
             </List>
            </Group>






  <Group>
<Div>Ингредиенты:
<br />• Капуста — 500-800 г.
<br />• Лук репчатый — 1 шт.
<br />• Морковь — 1-3 шт.
<br />• Сок томатный — 100 мл.
<br />• Рис (круглый) — 100 г.
<br />• Масло оливковое — 100 мл.
<br />• Петрушка (зелень).
<br />• Соль.
<br />• Перец черный (горошком).
<br />• Кориандр (в зёрнах или молотый).
<br />• Перец чили (по желанию).
<br />• Лук зеленый — 2-3 шт.
</Div>
</Group>

         <Group>
<Div>Рецепт: 
<br />• Нашинковать капусту. 
<br /> • Нарезать лук крупными кубиками, 
<br /> Отрегулировать количество
<br /> моркови по своему вкусу.
<br />• На оливковом масле постепенно
<br />  спассеровать лук, пока он  
<br /> не станет прозранчым, морковь. 
<br /> • Добавьте зеленый лук. 
<br />• Добавить капусту и жарить 
<br /> на максимальном огне, 
<br /> не забывая помешивать  
<br /> до карамелизации капусты.
<br />• Когда капуста немного поджарилась, 
<br /> добавить томатный сок и 
<br /> специи. 
<br />• Перемешайте.  
<br />• Уменьшите огонь до минимума.
<br /> Добавьте круглый рис и 
<br /> воды, вдвое больше чем риса. 
<br /> Закрыть крышкой и тушить 
<br />до готовности риса.
<br />• В конце блюдо посыпать 
<br /> зеленью, петрушкой. 

</Div>
</Group>


	</Panel>
);
Pirog.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
};

export default Pirog;
