import React from 'react';
import PropTypes from 'prop-types';
import { Panel, PanelHeader, HeaderButton, Link,  Button, Div,  platform, IOS, Group, Cell, List} from '@vkontakte/vkui';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';
import Icon24BrowserForward from '@vkontakte/icons/dist/24/browser_forward';
import Icon24Newsfeed from '@vkontakte/icons/dist/24/newsfeed';
import Icon24User from '@vkontakte/icons/dist/24/user';

const osname = platform();

const Mys = props => (
	<Panel id={props.id}>
		<PanelHeader
			left={<HeaderButton onClick={() => window.history.back()}>
				{osname === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}
			</HeaderButton>}
		>
			Картофель 	
		</PanelHeader>
<Group>
<List>
<Cell before={<Icon24User fill="#4bb34b" />} indicator={<Link component = "a" href = "https://www.spletenie.ru/users/larik-malasha/povarenok/">larik_malasha</Link>}>Автор  </Cell>
<Cell before={<Icon24Newsfeed fill="#4bb34b" />} indicator={<Link component = "a" href = "https://www.povarenok.ru">povarenok.ru</Link>}>Ресурс </Cell>

</List>
</Group> 




<Group>
              <List>
                <Cell indicator="32 минуты">Время приготовления </Cell>
             </List>
            </Group>

<Group>
<Div>Ингредиенты:
<br />• Приправа — по вкусу
<br />• Специи
<br />• Сухари панировочные — 4 ст. л.
<br />• Масло растительное — 4 ст. л.
<br />• Картофель — 5-6 шт
<br />• Соль — по вкусу
<br />• Перец черный — по вкусу
</Div>
</Group>

         <Group>
<Div>Рецепт: 
<br />• Картофель почистить, промыть 
<br /> и просушить. 
<br /> • Если картофель молодой, 
<br /> то можно просто тщательно помыть 
<br /> и нарезать прямо с кожурой. 
<br />• Добавить растительное масло, 
<br /> сухари, 
<br /> соль, перец и приправы.
<br />• Все хорошенько перемешать, 
<br />выложить на противень, 
<br />застеленный бумагой. 
<br />• Выпекать до золотистой корочки 
<br /> в нагретой духовке при 180-200 гр.

</Div>
</Group>
	</Panel>
);

Mys.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
};

export default Mys;
