import React from 'react';
import PropTypes from 'prop-types';
import {Panel, PanelHeader, HeaderButton, Search, ScreenSpinner, ListItem, UsersStack, Epic, Div, CellButton, FixedLayout, TabbarItem,  Tabbar,  platform, TabsItem,  Tabs, IOS, Cell, List, View, Group, Footer, Avatar, Button} from '@vkontakte/vkui';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';
import Icon24Mention from '@vkontakte/icons/dist/24/mention';
import Icon24User from '@vkontakte/icons/dist/24/user';
import Icon24Add from '@vkontakte/icons/dist/24/add';
import Icon24List from '@vkontakte/icons/dist/24/list';
import Icon28AddOutline from '@vkontakte/icons/dist/28/add_outline';

const osname = platform();

const Zav = ({ go, nickname, fetchedUser, onClick, usersTop,  userAcc, name, updateUserNickname, id}) => (


    <Panel id={id}>
    <PanelHeader

    left={<HeaderButton onClick={() => window.history.back()}>
        {osname === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}
      </HeaderButton>}
           
        >
           Ужин 
        </PanelHeader>

        <Group>
              <List>
                <Cell expandable onClick={go} data-to="pirog">Лаханоризо</Cell>
              </List>
            </Group>



           </Panel>
);

Zav.propTypes = {
    id: PropTypes.string.isRequired,
    go: PropTypes.func.isRequired,
};

export default Zav;
