import React from 'react';
import PropTypes from 'prop-types';
import { Panel, PanelHeader, HeaderButton, Button, Div, Link,  platform, IOS, Group, Cell, List} from '@vkontakte/vkui';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';
import Icon24BrowserForward from '@vkontakte/icons/dist/24/browser_forward';
import Icon24Newsfeed from '@vkontakte/icons/dist/24/newsfeed';
import Icon24User from '@vkontakte/icons/dist/24/user';

const osname = platform();

const Tul = props => (
	<Panel id={props.id}>
		<PanelHeader
			left={<HeaderButton onClick={() => window.history.back()}>
				{osname === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}
			</HeaderButton>}
		>
			Тульский пряник
		</PanelHeader>
<Group>
<List>
<Cell before={<Icon24User fill="#4bb34b" />} indicator={<Link component = "a" href = "https://www.spletenie.ru/users/tamara-shepeleva/povarenok/">Tamara Shepeleva</Link>}>Автор  </Cell>
<Cell before={<Icon24Newsfeed fill="#4bb34b" />} indicator={<Link component = "a" href = "https://www.povarenok.ru">povarenok.ru</Link>}>Ресурс </Cell>

</List>
</Group> 

<Group>
              <List>
                <Cell indicator="1 час">Время приготовления </Cell>
             </List>
            </Group>






<Group>
<Div>Ингредиенты:
<br /> • Масло сливочное — 100 г.
<br /> • Мед — 2 ст. л.
<br /> • Молоко — 1/2 стак.
<br /> • Сахар — 1 стак.
<br /> • Сода — 1 ч. л.
<br /> • Яйцо куриное — 1 шт.
<br /> • Корица — 1/2 ч. л.
<br /> • Орех мускатный — 1/2 ч. л.
<br /> • Мука пшеничная — 3 стак.
<br /> • Джем — по желанию. 
</Div>
</Group>

         <Group>
<Div>Рецепт: 
<br />• Яичный белок смешать с сахаром. 
<br />Желток отложить на время. 
<br />• Добавить молоко и размешать. 
<br />• Сюда же добавить мед и 
<br />растопленное масло. 
<br />• Затем добавляем специи.
<br />• Добавить соду. 
<br />• Затем просеянную муку.
<br />• Замесить тесто и поделить 
<br /> его пополам.
<br />• Одну часть раскатать в 
<br /> тонкий пласт. 
<br />Желательно тесто раскатывать на 
<br />бумаге для выпечки, 
<br />чтобы легче было перенести
<br />на противень.  
<br />• Перенести тесто на противень и 
<br />намазать повидлом.
<br />• Раскатать также 
<br />вторую часть теста. 
<br />Аккуратно перености ее на 
<br />противень и 
<br />накрыть первую половину. 
<br />Бумагу удалить.
<br />•Сделать прянику окантовку, 
<br />прижимая тесто по краям. 
<br />Сверху пряник,
<br />обмазать желтком,
<br />•Узор можно, 
<br /> нарисовать вилкой,
<br />•Выпекать пряник при t-180°, 
<br />приблизительно 30 минут. 



</Div>
</Group>

	</Panel>
);
Tul.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
};

export default Tul;
