import React from 'react';
import connect from '@vkontakte/vkui-connect';
import { View, Alert, ScreenSpinner, PanelHeader, Cell, Footer,  FixedLayout, Search, List, Link, CellButton, Group, Panel,  TabbarItem, Epic,  Tabbar, Spinner} from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css';

import Icon24Home from '@vkontakte/icons/dist/24/home';
import Icon24Services from '@vkontakte/icons/dist/24/services';
import Home from './panels/Home';
import Persik from './panels/Persik';
import House from './panels/House';
import Pet from './panels/Pet';
import Mys from './panels/Mys';
import Pyr from './panels/Pyr';
import Svin from './panels/Svin';
import Yz from './panels/Yz';
import Zapek from './panels/Zapek';
import Veng from './panels/Veng';
import Tul from './panels/Tul';
import Ob from './panels/Ob';
import Pirog from './panels/Pirog';
import Chip from './panels/Chip';
import Hom from './panels/Hom';
import Popa from './panels/Popa';
import Vse from './panels/Vse';
import Test from './panels/Test';
import Lol from './panels/Lol';
import Es from './panels/Es';
import Min from './panels/Min';
import Zav from './panels/Zav';
import Ab from './panels/Ab';



class App extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            activePanel: 'home', 
            usersTopC: `[{"id":"13","title":"test","user_id":"140933159","message":"dengi"}]`
        };
        }
    

    componentDidMount() {
       window.addEventListener('popstate', e => e.preventDefault() & this.menu(e));
        window.history.pushState( { panel: 'home' }, `home` );
        connect.subscribe((e) => {
            switch (e.detail.type) {
                case 'VKWebAppGetUserInfoResult':
                    this.setState({ fetchedUser: e.detail.data });
                    //console.log(e.detail.data)
                    this.getUsers();
                    break;
                    case 'VKWebAppShowWallPostBoxResult':

                    break;
                default:
                    console.log(e.detail.type);
            }
        });
        connect.send('VKWebAppGetUserInfo', {});
    }

 onHeaderEditClick() {
    this.setState({ editing: !this.state.editing });
  }

  getForm() {
    return new Promise((resolve) => setTimeout(() => resolve(), 1000));
  }


    go = (e) => {
        this.setState({ activePanel: e.currentTarget.dataset.to })
        window.history.pushState( { panel: e.currentTarget.dataset.to }, `${e.currentTarget.dataset.to}` );
    }

    menu (e) {
        if(e.state) {
            this.setState( { activePanel: e.state.panel } );
        } else {
            this.setState( { activePanel: 'home', search: '' } );
            window.history.pushState( { panel: 'home' }, `home` );
        }
    }

render() {
        return (
            <View popout={this.state.popout} activePanel={this.state.activePanel}>
                <Home id="home" change={this.ChangeSearch} go={this.go} state={this.state} />
                <Persik id="persik" go={this.go} />
                <House id="house" go={this.go} />
                <Pet id="pet" go={this.go} />
                <Mys id="mys" go={this.go} />
                <Svin id="svin" go={this.go} />
                <Yz id="yz" go={this.go} />
                <Zapek id="zapek" go={this.go} />
                <Veng id="veng" go={this.go} />
                <Pyr id="pyr" go={this.go} />
                <Tul id="tul" go={this.go} />
                <Ob id="ob" go={this.go} />
                <Pirog id="pirog" go={this.go} />
                <Chip id="chip" go={this.go} />
                <Hom id="hom" go={this.go} />
                <Vse id="vse" go={this.go} />
                <Test id="test" go={this.go} />
                <Lol id="lol" go={this.go} />
                <Es id="es" go={this.go} />
                <Zav id="zav" go={this.go} />
                <Min id="min" go={this.go} />
                <Ab id="ab" go={this.go} />
                <Popa id="popa" go={this.go} change={this.ChangeSearch} state={this.state}/>


            </View>
               );
    }
}

export default App;
