import React from 'react';
import PropTypes from 'prop-types';
import { Panel, PanelHeader, HeaderButton, Checkbox, Link,  Group, CellButton, FormStatus, openPopout,  Textarea, FixedLayout,  platform, Tabs, TabsItem,   IOS, Cell, List, FormLayout, Button, Div, Input, FormLayoutGroup } from '@vkontakte/vkui';
import './Persik.css';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';
import Icon24BrowserForward from '@vkontakte/icons/dist/24/browser_forward';
import Icon24Home from '@vkontakte/icons/dist/24/home';
import Icon24LogoTwitter from '@vkontakte/icons/dist/24/logo_twitter';
import Icon24Coins from '@vkontakte/icons/dist/24/coins';
import Icon24Favorite from '@vkontakte/icons/dist/24/favorite';
import Icon24Newsfeed from '@vkontakte/icons/dist/24/newsfeed';
