const osname = platform();



const Persik = ({ go, id, handleTransferChange, handleReputationSubmit, handleNickChange, handleNickSubmit, userAcc }) => (
   <Panel id={id}>
      <PanelHeader
      left={<HeaderButton onClick={() => window.history.back()}>
        {osname === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}
      </HeaderButton>}

      >
     Сырные лепешки
      </PanelHeader>
 
<Group>
<List>
<Cell before={<Icon24User fill="#4bb34b" />} indicator={<Link component = "a" href = "https://www.spletenie.ru/users/olga-babich/povarenok/">Ольга Бабич</Link>}>Автор  </Cell>
<Cell before={<Icon24Newsfeed fill="#4bb34b" />} indicator={<Link component = "a" href = "https://www.povarenok.ru">povarenok.ru</Link>}>Ресурс </Cell>

</List>
</Group> 


<Group>
<List>
<Cell indicator="30 минут">Время приготовления </Cell>
</List>
</Group>



<Group>
<Div>Ингредиенты:
<br />•  Кефир — 1 стак.
<br />• Соль — 0,5 ч. л.
<br />• Сахар — 0,5 ч. л.
<br />• Сода — 0,5 ч. л.
<br />• Сыр твердый (тертый) — 1 стак.
<br />• Ветчина, колбаска,
<br /> тертые на терке - 1 стак. 
<br />• Мука пшеничная - 2 стак. </Div>
</Group>

         <Group>
<Div>Рецепт: 
<br />• В кефир добавить соль, сахар, 
<br /> соду, хорошо перемешать.      
<br />• Добавить сыр  и муку.
<br /> Также хорошо перемешать.
<br />• Полученное тесто делим на 
<br /> колобки, раскатываем небольшую 
<br />лепешку, кладем начинку. 
<br /> Соединяем  края и немного раскатываем.
<br />• Жарить в предварительно 
<br /> разогретой сковороде 
<br /> на масле с 2-х сторон,  
<br />на среднем огне, накрыв крышкой.

</Div>
</Group>



      </Panel>


);

Persik.propTypes = {
    id: PropTypes.string.isRequired,
    go: PropTypes.func.isRequired,
};

export default Persik;
