import Icon24Cancel from '@vkontakte/icons/dist/24/cancel';

import Icon16Dropdown from '@vkontakte/icons/dist/16/dropdown';

function openTab(url) {

// Create link in memory

let a = window.document.createElement("a");

a.target = '_blank';

a.href = url;

// Dispatch fake click

let e = window.document.createEvent("MouseEvents");

e.initMouseEvent("click", true, true, window, 0, 0, 0, 0, 0, false, false, false, false, 0, null);

a.dispatchEvent(e);

};






const osname = platform();
let thematics = [
    {
        "name": "Выпечка",
        "to": "yz"
    },
    {
        "name": "Жареное",
        "to": "ob"
    },
    {
        "name": "Ужин",
        "to": "zav"
    }, 
    {
        "name": "Быстрое приготовление",
        "to": "min"
    },
    {
        "name": "Обед",
        "to": "ab"
    },
]

class Es extends React.Component{
    constructor(props){
        super(props)
        this.state = {  
        activePanel: 'es', 
        activeStory: 'home', 
        search: ''
        }
        this.onChange = this.onChange.bind(this);

   }
    onChange (search) { this.setState({ search }); }


    get thematics () {
      const search = this.state.search.toLowerCase();
      return thematics.filter(({name}) => name.toLowerCase().indexOf(search) > -1);
    }
    render(){
        let { go, nickname, fetchedUser, onClick, usersTop,  userAcc, name, updateUserNickname, id} = this.props;
        return(
                <Panel id={id}>
    <PanelHeader
      left={<HeaderButton onClick={() => window.history.back()}>
        {osname === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}
      </HeaderButton>}
    >
      Категории
    </PanelHeader>



                   <FixedLayout vertical="top">
                    <Search  maxlength="20" theme="default" value={this.state.search} onChange={this.onChange} />
                    </FixedLayout>
                    

                <Group style={{marginTop: "50px"}}>
                      {this.thematics.length > 0 &&
                        <List>
                          {this.thematics.map((thematic, i) => <Cell key={i} expandable onClick={go} data-to={thematic.to}>{thematic.name}</Cell>)}
                        </List>
                      }
                      
                      </Group>
                                            
<Group>
              <List>
                <Cell   indicator="Скоро" >Напитки</Cell>
                <Cell  indicator="Скоро">Десерты</Cell>
              </List>
            </Group>



                        <Footer>
            
                     <Button onClick={() => openTab("https://vk.me/club184207972")}  level="outline">Предложить рецепт</Button>
            
                    </Footer>

  </Panel>
        )
    }
}

Es.propTypes = {
    id: PropTypes.string.isRequired,
    go: PropTypes.func.isRequired,
};

export default Es;