import React from 'react';
import PropTypes from 'prop-types';
import {Panel, PanelHeader, HeaderButton, Spinner, Search, ScreenSpinner, ListItem, UsersStack, Epic, Div, CellButton, FixedLayout, TabbarItem,  Tabbar,  platform, TabsItem,  Tabs, IOS, Cell, List, View, Group, Footer, Avatar, Button} from '@vkontakte/vkui';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';
import Icon24Mention from '@vkontakte/icons/dist/24/mention';
import Icon24User from '@vkontakte/icons/dist/24/user';
import Icon24Add from '@vkontakte/icons/dist/24/add';
import Icon24List from '@vkontakte/icons/dist/24/list';
import Icon28AddOutline from '@vkontakte/icons/dist/28/add_outline';
import Icon24Filter from '@vkontakte/icons/dist/24/filter';
import Icon16Verified from '@vkontakte/icons/dist/16/verified';
import Icon16Smile from '@vkontakte/icons/dist/16/smile';
import Icon16CheckCircle from '@vkontakte/icons/dist/16/check_circle';
const osname = platform();

const Hom = ({ go, nickname, fetchedUser, onClick, usersTop,  userAcc, name, updateUserNickname, id}) => (
 
    <Panel id={id}>
    
            <PanelHeader
          left={<HeaderButton onClick={() => window.history.back()}>
        {osname === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}
            </HeaderButton>}
        >
           Настройки     
        </PanelHeader>
<Group>
      <CellButton component = "a" href = "https://vk.me/club184207972" before={<Icon24Add />}>Предложить рецепт</CellButton>
    </Group>
  

          <Group>
      <Div>
      В данный момент в Сервисе собраны не все рецепты. 
      Для того, чтобы предложить свой рецепт, воспользуйтесь кнопкой выше. 
        
      </Div>
</Group>    
  

      <Group>
        <List>
          <Cell
            before={<Avatar style={{ background: 'var(--accent)' }} size={28}><Icon16CheckCircle fill="var(--white)" /></Avatar>}
            description = "2.0 (Beta)"
          >
            Версия Сервиса: 
          </Cell>
        </List>
      </Group>




           </Panel>



);

Hom.propTypes = {
    id: PropTypes.string.isRequired,
    go: PropTypes.func.isRequired,
};

export default Hom;
