import React from 'react';
import PropTypes from 'prop-types';
import { Panel, PanelHeader, HeaderButton, Link, Button, Div,  platform, IOS, Group, Cell, List} from '@vkontakte/vkui';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';
import Icon24BrowserForward from '@vkontakte/icons/dist/24/browser_forward';
import Icon24Newsfeed from '@vkontakte/icons/dist/24/newsfeed';
import Icon24User from '@vkontakte/icons/dist/24/user';

const osname = platform();

const Chip = props => (
	<Panel id={props.id}>
		<PanelHeader
			left={<HeaderButton onClick={() => window.history.back()}>
				{osname === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}
			</HeaderButton>}
		>
			Чипсы
		</PanelHeader>
<Group>
<List>
<Cell before={<Icon24User fill="#4bb34b" />} indicator={<Link component = "a" href = "https://www.spletenie.ru/users/irrez/povarenok/">иррез</Link>}>Автор  </Cell>
<Cell  before={<Icon24Newsfeed fill="#4bb34b" />} indicator={<Link component = "a" href = "https://www.povarenok.ru">povarenok.ru</Link>}>Ресурс </Cell>

</List>
</Group>


 <Group>
              <List>
                <Cell indicator="5 минут">Время приготовления </Cell>
             </List>
            </Group>





  <Group>
<Div>Ингредиенты:
<br />• Картофель (средний) — 2-3 шт
<br />• Специи (по желанию)
<br />• Соль (по желанию)
</Div>
</Group>

         <Group>
<Div>Рецепт: 
<br />• Вымойте картофель щеткой, 
<br /> а очищать от кожуры или нет, 
<br /> решайте сами.
<br /> • Нарежьте кружками или дольками 
<br /> как можно тоньше - удобнее всего 
<br />  делать это слайсером 
<br /> или специальной теркой с 
<br /> держателем для продукта, 
<br /> которую повара называют 
<br />  "мандолина". 
<br />• На бумагу для выпечки (пергамент) 
<br /> выложите в один слой 
<br />  ломтики картофеля.
<br />• Включите микроволновку 
<br /> на мощность 700 Вт, 
<br /> готовьте 3-5 минут.

</Div>
</Group>

	</Panel>
);
Chip.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
};

export default Chip;
