import React from 'react';
import PropTypes from 'prop-types';
import { Panel, PanelHeader, HeaderButton, Button, Link,  Div,  platform, IOS, Group, Cell, List} from '@vkontakte/vkui';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';
import Icon24BrowserForward from '@vkontakte/icons/dist/24/browser_forward';
import Icon24Newsfeed from '@vkontakte/icons/dist/24/newsfeed';
import Icon24User from '@vkontakte/icons/dist/24/user';

const osname = platform();

const Svin = props => (
	<Panel id={props.id}>
		<PanelHeader
			left={<HeaderButton onClick={() => window.history.back()}>
				{osname === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}
			</HeaderButton>}
		>
			Свинина по-барски
		</PanelHeader>
<Group>
<List>
<Cell before={<Icon24User fill="#4bb34b" />} indicator={<Link component = "a" href = "https://www.spletenie.ru/users/best/povarenok/">best</Link>}>Автор  </Cell>
<Cell before={<Icon24Newsfeed fill="#4bb34b" />} indicator={<Link component = "a" href = "https://www.povarenok.ru">povarenok.ru</Link>}>Ресурс </Cell>

</List>
</Group>




<Group>
              <List>
                <Cell indicator="45 минут">Время приготовления </Cell>
             </List>
            </Group>
<Group>
<Div>Ингредиенты:
<br /> • Мясо — 500 г
<br /> • Огурец соленый — 1 шт
<br /> • Орехи грецкие (измельченные ядра) — 2 ст. л.
<br /> • Зелень (укроп, петрушка) — 1 пуч.
<br /> • Чеснок — 2 зуб.
<br /> • Майонез — 3 ст. л.
</Div>
</Group>

         <Group>
<Div>Рецепт: 
<br />• Мясо порезать порционными 
<br /> кусками. 
<br /> Отбить молотком, посолить, 
<br /> поперчить.
<br /> Обжарить с двух сторон 
<br />до полуготовности.
<br /> • Приготовить соус: 
<br /> мелко порезать зелень, 
<br />огурец, орехи, добавить чеснок. 
<br />Добавить майонез, 
<br />чтобы получилась густая масса.
<br />• Мясо выложить в форму, 
<br />сверху толстым слоем положить соус, 
<br />и поставить в духовку 
<br />при температуре 220*С 
<br /> минут на 15-30.

</Div>
</Group>
	</Panel>
);
Svin.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
};

export default Svin;
