import React from 'react';
import PropTypes from 'prop-types';
import { Panel, PanelHeader, HeaderButton, Link, Button, Div,  platform, IOS, Group, Cell, List} from '@vkontakte/vkui';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';
import Icon24BrowserForward from '@vkontakte/icons/dist/24/browser_forward';

import Icon24Newsfeed from '@vkontakte/icons/dist/24/newsfeed';
import Icon24User from '@vkontakte/icons/dist/24/user';

const osname = platform();

const Pyr = props => (
	<Panel id={props.id}>
		<PanelHeader
			left={<HeaderButton onClick={() => window.history.back()}>
				{osname === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}
			</HeaderButton>}
		>
			Суп-пюре из кабачков	
		</PanelHeader>
<Group>
<List>
<Cell before={<Icon24User fill="#4bb34b" /> }indicator={<Link component = "a" href = "https://www.spletenie.ru/users/ljudmila-nk/povarenok/">Людмила НК</Link>}>Автор  </Cell>
<Cell before={<Icon24Newsfeed fill="#4bb34b" />} indicator={<Link component = "a" href = "https://www.povarenok.ru">povarenok.ru</Link>}>Ресурс </Cell>

</List>
</Group>

<Group>
              <List>
                <Cell indicator="25 минут">Время приготовления </Cell>
             </List>
            </Group>





<Group>
<Div>Ингредиенты:
<br /> • Бульон (куриный) — 1 л
<br /> • Сливки — 200 мл
<br /> • Масло растительное — 2 ст. л.
<br /> • Чеснок — 1-2 зуб.
<br /> • Лук репчатый — 1 шт
<br /> •Морковь — 1 среднюю или 2 маленьких молодых морковки
<br />маленьких молодых морковки — 1-2 шт
<br /> • Картофель — 1-2 шт
<br /> • Кабачок (молодой) — 4-5 шт
<br /> • Специи
<br /> • Гренки
</Div>
</Group>

         <Group>
<Div >Рецепт: 
<br />• Кабачки, морковь, картофель, 
<br /> лук почистить.
<br /> • В кастрюле разогреть 
<br /> растительное масло, 
<br />обжарить раздавленные 
<br /> чеснок и лук. Нарезать кабачки, 
<br /> морковь, картофель, 
<br />  отправить в кастрюлю.
<br />• Помешивая, тушить минут 7.
<br />• Добавить бульон и 
<br /> довести до готовности овощей.  
<br />• Разогреть сливки.
<br />• Отлить бульон, отставить, 
<br /> а овощи взбить блендером до пюре.
<br />• В кастрюлю с овощами 
<br /> добавляем горячие сливки и 
<br /> бульон до нужной консистенции, 
<br /> прогреваем, помешивая. 

</Div>
</Group>


	</Panel>
);
Pyr.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
};

export default Pyr;
